.error_msg {
    max-height: 0;
    overflow: hidden;
    

    &__txt {
        background-color: $white;
        border: 1px solid red;
        color: red;
        padding: 10px 30px;
        display: inline-block;
    }

    &.shown {
        max-height: 40px;
        transition: max-height .3s ease-in-out;

        @media screen and (max-width: $sm) {
            max-height: 50px;
          }
    }
}